// Font weight help class

.fw-medium {
    font-weight: $font-weight-medium;
}

.fw-semibold {
    font-weight: $font-weight-semibold;
}

// Social

.social-list-item {
    height: 2rem;
    width: 2rem;
    line-height: calc(2rem - 4px);
    display: block;
    border: 2px solid $gray-500;
    border-radius: 50%;
    color: $gray-500;
    text-align: center;
    transition: all 0.4s;
    &:hover {
        color: $gray-600;
        background-color: $gray-200;
    }
}

.w-xs {
    min-width: 80px;
}
.w-sm {
    min-width: 95px;
}
.w-md {
    min-width: 110px;
}
.w-lg {
    min-width: 140px;
}

.w-xl {
    min-width: 160px;
}

.bg-overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
    background-color: rgba($dark, 0.7);
}

.border-primary {
    border-color: rgba(var(--bs-primary-rgb), 1) !important;
}

// border-color
.border-dark {
    border-color: var(--#{$variable-prefix}dark) !important;
}
.border-light {
    border-color: var(--#{$variable-prefix}light) !important;
}

[data-layout-mode="dark"] {
    .text-body {
        color: var(--#{$variable-prefix}gray-500) !important;
    }
    .btn-close {
        filter: invert(1) grayscale(100%) brightness(200%);
    }
}
